
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import layoutNavbar from '@/layout/navbar'
import layoutNavbarShadow from '@/layout/navbar-shadow'
import widgetScrollBox from '@/widget/scroll-box'

export default {
components: {
Group,
XInput,
XTextarea,
layoutNavbar,
layoutNavbarShadow,
widgetScrollBox
},
data () {
return {
title: '',
content: ''
}
},
computed: {
disabled () {
const { title, content } = this
if (title && content) {
return false
} else {
return true
}
}
},
methods: {
async navbarTap ({ type }) {
if (type === 'right') {
const { now } = this.$
const { name, checkcode } = this.$storage.user
const key = `${now}${checkcode}`
const born = now
const uid = checkcode
const wid = window.util.uuid(4)
const title = encodeURIComponent(this.title)
const content = encodeURIComponent(this.content)
const value = {
uid,
wid,
born,
name,
title,
content,
tag: [],
type: '',
isRemove: false,
isSticky: false
}
this.$set(this.$storage.wish.map, key, value)
await this.$forage({ type: 'update', key: 'wish@map' })
const text = this.$t('sdk-toast@create-success')
this.$sdk.toast({ text })
this.$pop()
}
}
}
}



